import { dfRequest } from '../request'

export interface RecordsItem {
  id: number
  username: string
  startTime: string
  endTime: string
  valid: boolean
  isRecord: boolean
  timeLength: string
}

export function getUserTrainInfo(): Promise<RecordsItem[] | undefined> {
  return new Promise((resolve) => {
    dfRequest
      .post({
        url: '/api/getrecord/'
      })
      .then((res) => {
        // console.log('个人训练记录', res)
        resolve(res.data.records)
      })
      .catch((err) => {
        console.log('获取个人训练记录失败', err)
      })
  })
}
